import hexSha1 from 'hex-sha1';
import uniGrid from "@/components/uni-grid/uni-grid.vue"
import {ChangePageTitle} from '../../title.js'
export default {
	data() {
		return {
			back1: true,
			title: '联系人详情',
			accessToken: '64e9275246c6545a3b5d3ad060fe4c516f44d556',
			appsercert: 'MTExMTEx',
			id: '',
			showoutborder: false,
			showborder: true,
			columnnum: 4,
			detail: {},
			sha: ''
		}
	},
	components: {
		uniGrid
	},
	methods: {
		getdetail() {
			var signStr =
				"access-token=" +
				this.accessToken +
				"&appsercert=" +
				this.appsercert +
				"&id=" + this.id;
			console.log(signStr)
			var sha = hexSha1(signStr);
			console.log(sha)
			this.$axios({
					url: this.$baseUrl.persondetail.url + '?access-token=' + this.accessToken + '&sign=' + sha + '&id=' + this.id,
					method: 'post'
				}, {
					id: this.id
				})
				.then(res => {
					console.log(res)
					this.detail = res.data.contact
				}, (error) => {
					console.log(error)
				})
		},
		click(val) {
			console.log(val)
		}
	},
	onLoad(e) {
		this.id = e.id
		this.getdetail()
	},
	onShow() {
		document.title = '联系人详情'
		ChangePageTitle('联系人详情')
	}
}
